<template>
<section class="quiz">
    <div class="container">
        <div class="quiz__page">
            <ul class="quiz__page-list">
                <!-- <li><button class="finished">1</button></li> -->
                <!-- <li><button class="active">2</button></li> -->
                <li v-for="(question, index) in questions" :key="index"><button>{{index+1}}</button></li>
            </ul>
        </div>
        <header class="quiz__top">
            <h3 class="track__title">{{name}}</h3>
        </header>
        <div class="quiz__inner">
            <div>
                <div class="quiz__inner-header" v-html="questions[questionIndex].content">{{questions[questionIndex].content}}</div>
                <ul>
                    <li class="quiz__choice" v-for="(answer, i) in questions[questionIndex].answers" :key="i">
                        <div>
                            <vs-checkbox v-model="clientAnswers[i]" :style="{marginTop: '5px', marginLeft: '0px', marginRight: '0px'}"></vs-checkbox>
                        </div>
                        <label class="container-box" :style="{paddingLeft: '2px'}">
                            <strong>{{ i+1 }}</strong>
                        </label>
                        <p v-html="answer.content">{{answer.content}}</p>
                    </li>
                </ul>
            </div>
            <div class="quiz__inner--footer">
                <div class="send-right">
                   <vs-button color="dark" type="border" icon-pack="feather" icon="icon-check" :style="{marginRight:'5px'}" @click="submitAnswer(questionIndex)">Done</vs-button>
                    <div class="next">
                        <button @click="moveNextQuestion">Next question
                            <svg xmlns="http://www.w3.org/2000/svg" width="20" height="10" viewBox="0 0 20 10">
                                <path id="black-arrow" fill="#ffffff" d="M15.012,3.75H0v2.5H15.012V10L20,5,15.012,0Z"></path>
                            </svg>
                        </button>
                    </div>
                </div>
            </div>
        </div>
			
	</div>
</section>
</template>
<script>
import { GETESTITEM } from '@/store/actionType';
// import { mapState } from 'vuex';
export default {
 data() {
     return {
         name: '',
         questions: [],
         questionIndex: 0,
         rightCounter: 0,
         clientAnswers: [],
         currentQuestion: Object,
         totalResult: ''
     }
 },
 computed: {
    //  ...mapState({
    //      testQuizItems: state => state.client.getTestItems
    //  })
 },
 created() {
     const quizId = this.$router.currentRoute.params.id;
     this.$store.dispatch(GETESTITEM, quizId).then(
         res => {
            this.name = res.data.name;
            this.questions = res.data.questions;
            this.questions.map(function(item) {
                console.log(item);
            })
            this.totalResult = this.questions.length;
         }
     );
 },
 methods: {
    moveNextQuestion() {
        this.questionIndex++;
    },
    checkAnswer(i) {
        console.log(i);
        console.log(document.getElementsByClassName('answer').value);
    },
    submitAnswer(questionIndex) {
        console.log(this.clientAnswers, this.questions[questionIndex]);
        this.currentQuestion = this.questions[questionIndex];
        for(let i = 0; i < this.currentQuestion.answers.length; i++) {
            if(this.clientAnswers[i]) {
                if(this.clientAnswers[i] == this.currentQuestion.answers[i]) {
                    continue;
                }
            }
        }
    }
 }  
}
</script>
	

